}


code ,
pre  {
    background: #f3f3f3;
    font-family: 'Andale Mono', 'Ubuntu Mono', monospace;
    text-align: left;
    white-space: pre;
    word-spacing: normal;
    word-break: normal;
    word-wrap: normal;
    line-height: 2;
    -moz-tab-size: 4;
    -o-tab-size: 4;
    tab-size: 4;
    -webkit-hyphens: none;
    -moz-hyphens: none;
    -ms-hyphens: none;
    hyphens: none;
}

pre {
    overflow-x: auto;
    padding: 2em;
}

pre  {
    padding: .5em;
    margin: .5em 0;
}